<template>
	<v-card>
		<v-card-title class="py-2">
			Edit Your Profil Startup
		</v-card-title>
		<v-form v-model="valid" ref="form">
			<v-card-text>
				<v-text-field
          v-model="dataIncubator.name"
          label="Name"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          v-model="dataIncubator.name"
          label="Location"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataIncubator.name"
          label="Phone"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataIncubator.name"
          label="E-mail"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          v-model="dataIncubator.name"
          label="Website"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>  
        <v-text-field
          v-model="dataIncubator.name"
          label="Instagram"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataIncubator.name"
          label="Facebook"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          v-model="dataIncubator.name"
          label="Youtube"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>  
        <v-text-field
          v-model="dataIncubator.name"
          label="Since (yyyy-mm-dd)"
          dense
          :rules="dateRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          v-model="dataIncubator.name"
          label="Total User"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          v-model="dataIncubator.name"
          label="Total Employee"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <img 
          class="mt-3" 
          src="@/assets/logo.svg" 
          alt="logo Startup"
          width="200" /> 
        <v-file-input
          accept="image/*"
          show-size
          counter
          :rules="requiredRule"
          label="Logo"
        ></v-file-input> 
        <v-textarea
          v-model="dataIncubator.name"
          label="About"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-textarea>  
        <div class="title mb-3">Profil Manager</div>                 
        <v-text-field
          v-model="dataIncubator.name"
          label="Name"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <img 
          class="mt-3" 
          src="@/assets/avatar.jpg" 
          alt="photo manager"
          width="200" />           
        <v-file-input
          accept="image/*"
          show-size
          counter
          :rules="requiredRule"
          label="Photo"
        ></v-file-input>
			</v-card-text>
			<v-card-actions>
				<v-spacer></v-spacer>
				<v-btn
          color="primary"
          text
          @click="$router.go(-1)">
          kembali
        </v-btn>
        <v-btn
          :disabled="!valid"
          color="primary"
          @click.prevent="validate"
          type="submit"
          depressed>
          simpan
        </v-btn>
			</v-card-actions>
		</v-form>
	</v-card>
</template>

<script>
	export default{
		data: () => ({
			valid: true,
			dataIncubator: {
				name: ''
			},
      requiredRule: [v => !!v || 'Data harus diisi']
		}),

		computed: {
      dateRule () {
        return [
          ...this.requiredRule,
          v => !!v.match(/^(19[5-9][0-9]|20[0-4][0-9]|2050)[-/](0?[1-9]|1[0-2])[-/](0?[1-9]|[12][0-9]|3[01])$/igm) || 'Harus berupa tanggal'
        ]
      }
    },

		methods: {
      validate () {
        if (this.$refs.form.validate()) {
          // console.log(this.detailKelas)
        }
      }
    }
	}
</script>